import { AppState, } from './components/AppData';
import { EventEmitter } from './components/base/events';
import { BasketItem, CatalogItem, ProductCard } from './components/Card';
import { Basket } from './components/common/Basket';
import { Modal } from './components/common/Modal';
import { Success } from './components/common/Success';
import { Contacts, Order } from './components/Order';
import { Page } from './components/Page';
import { ProductAPI } from './components/ProductApi';
import './scss/styles.scss';
import { PaymentMethod } from './types';
import { API_URL, CDN_URL } from './utils/constants';
import { cloneTemplate, createElement, ensureElement } from './utils/utils';
const events = new EventEmitter();
const api = new ProductAPI(CDN_URL, API_URL);
// Чтобы мониторить все события, для отладки
events.onAll(({ eventName, data }) => {
    console.log(eventName, data);
});
// Все шаблоны
const cardCatalogTemplate = ensureElement('#card-catalog');
const cardPreviewTemplate = ensureElement('#card-preview');
const basketTemplate = ensureElement('#basket');
const orderTemplate = ensureElement('#order');
const successTemplate = ensureElement('#success');
const contactsTemplate = ensureElement('#contacts');
const cardBasketTemplate = ensureElement('#card-basket');
// Модель данных приложения
const appData = new AppState(events);
// Глобальные контейнеры
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
// Переиспользуемые части интерфейса
const basket = new Basket(cloneTemplate(basketTemplate), events);
const order = new Order(cloneTemplate(orderTemplate), events);
const contacts = new Contacts(cloneTemplate(contactsTemplate), events);
// Дальше идет бизнес-логика
// Поймали событие, сделали что нужно
// Изменились элементы каталога
events.on('items:changed', () => {
    page.catalog = appData.catalog.map((item) => {
        const card = new CatalogItem(cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('card:select', item),
        });
        return card.render({
            title: item.title,
            image: item.image,
            description: item.description,
            price: item.price,
            category: item.category,
        });
    });
    page.counter = appData.getItemsCount();
});
// Отправлена форма контактной информации
events.on('contacts:submit', () => {
    api
        .orderProducts(appData.order)
        .then((result) => {
        const success = new Success(cloneTemplate(successTemplate), {
            onClick: () => {
                modal.close();
                appData.clearOrder();
            },
        });
        success.total = appData.order.items
            .map((item) => item.price)
            .reduce((sum, i) => sum + i, 0);
        modal.render({
            content: success.render({}),
        });
        appData.clearOrder();
    })
        .catch((err) => {
        console.error(err);
    });
});
// Изменилось состояние валидации формы
events.on('formErrors:change', (errors) => {
    const { email, phone } = errors;
    contacts.valid = !email && !phone;
    contacts.errors = Object.values({ phone, email })
        .filter((i) => !!i)
        .join('; ');
});
events.on('formErrors:change', (errors) => {
    const { address } = errors;
    order.valid = !address;
    order.errors = Object.values({ address })
        .filter((i) => !!i)
        .join('; ');
});
// Изменилось одно из полей
events.on(/^contacts\..*:change/, (data) => {
    appData.setOrderField(data.field, data.value);
});
events.on('order.address:change', (data) => {
    appData.setOrderField(data.field, data.value);
});
events.on('order.payment:change', (data) => {
    if (data.value === PaymentMethod.card) {
        appData.setPaymentMethod(PaymentMethod.card);
    }
    if (data.value === PaymentMethod.cash) {
        appData.setPaymentMethod(PaymentMethod.cash);
    }
});
// Открыть корзину
events.on('basket:open', () => {
    modal.render({
        content: createElement('div', {}, [basket.render()]),
    });
});
// Открыть форму заказа
events.on('order:open', () => {
    modal.render({
        content: order.render({
            payment: PaymentMethod.card,
            address: '',
            valid: false,
            errors: [],
        }),
    });
});
// Переключиться на форму контактной информации
events.on('order:submit', () => {
    events.emit('contacts:open');
});
// Открыть форму контактной информации
events.on('contacts:open', () => {
    modal.render({
        content: contacts.render({
            phone: '',
            email: '',
            valid: false,
            errors: [],
        }),
    });
});
// Изменения в корзине, но лучше все пересчитать
events.on('basket:changed', () => {
    page.counter = appData.getItemsCount();
    let total = 0;
    if (appData.order) {
        basket.items = appData.order.items.map((item, index) => {
            const card = new BasketItem(cloneTemplate(cardBasketTemplate), {
                onClick: (event) => {
                    appData.removeFromOrder(item.id);
                },
            });
            card.index = index + 1;
            total += item.price;
            return card.render({
                title: item.title,
                price: item.price,
            });
        });
    }
    else {
        basket.items = [];
    }
    basket.total = total;
});
// Открыть товар
events.on('card:select', (item) => {
    appData.setPreview(item);
});
// Изменен открытый выбранный лот
events.on('preview:changed', (item) => {
    const showItem = (item) => {
        const card = new ProductCard(cloneTemplate(cardPreviewTemplate), {
            onClick: () => {
                appData.addToOrder(item);
                modal.close();
            },
        });
        modal.render({
            content: card.render(item),
        });
    };
    if (item) {
        showItem(item);
    }
    else {
        modal.close();
    }
});
// Блокируем прокрутку страницы если открыта модалка
events.on('modal:open', () => {
    page.locked = true;
});
// ... и разблокируем
events.on('modal:close', () => {
    page.locked = false;
});
// Получаем товары с сервера
api
    .getProductList()
    .then(appData.setCatalog.bind(appData))
    .catch((err) => {
    console.error(err);
});
