import { Model } from './base/Model';
import { PaymentMethod, } from '../types/index';
export class ProductItem extends Model {
}
export class AppState extends Model {
    constructor(events) {
        super({}, events);
        this.catalog = [];
        this.preview = null;
        this.order = null;
        this.formErrors = {};
    }
    getProduct(productId) {
        return this.catalog.find((product) => product.id === productId);
    }
    setCatalog(items) {
        this.catalog = items.map((item) => new ProductItem(item, this.events));
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    getItemsCount() {
        if (this.order) {
            return this.order.items.length;
        }
        return 0;
    }
    addToOrder(product) {
        if (this.order) {
            if (!this.order.items.some((_product) => _product.id === product.id)) {
                this.order.items.push(product);
            }
        }
        else {
            this.order = {
                payment: PaymentMethod.card,
                address: '',
                email: '',
                phone: '',
                items: [product],
            };
            this.events.emit('order:updated', this.order);
        }
        this.events.emit('basket:changed', this.order);
    }
    removeFromOrder(productId) {
        if (this.order) {
            this.order.items = this.order.items.filter((item) => item.id !== productId);
            if (this.order.items.length === 0) {
                this.clearOrder();
            }
            this.events.emit('basket:changed', this.order);
        }
    }
    clearOrder() {
        this.order = null;
        this.events.emit('basket:changed');
    }
    setPreview(product) {
        this.preview = product.id;
        this.emitChanges('preview:changed', product);
    }
    setOrderField(field, value) {
        if (this.order) {
            this.order[field] = value;
            if (this.validateOrder()) {
                this.events.emit('order:ready', this.order);
            }
        }
    }
    setPaymentMethod(value) {
        if (this.order) {
            this.order.payment = value;
            this.events.emit('order:ready', this.order);
        }
    }
    validateOrder() {
        const errors = {};
        if (this.order) {
            if (!this.order.email) {
                errors.email = 'Необходимо указать email';
            }
            if (!this.order.phone) {
                errors.phone = 'Необходимо указать телефон';
            }
            if (!this.order.address) {
                errors.address = 'Необходимо указать адрес';
            }
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
